body {
  background-color: rgb(52, 52, 54);
  background-image: url("skull_image.png");
  background-size: 100px;
  background-attachment: fixed;
}
#header {
  padding: 0px;
  background-color: rgb(41, 41, 41);
  position: fixed;
  top: 0;
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#header h3 {
  color: #d2d2d2;
  float: left;
  font-style: italic;
  font-weight: bold;
  font-size: 25px;
}
.main {
  margin-top: 10%;
}
.App {
  background-color: rgb(41, 41, 41);
  min-height: 500px;
  width: 450px;
  margin: auto;
  border-radius: 15px;
  padding-bottom: 20px;
}
#to-do-list textarea {
  background-color: rgb(19, 121, 146);
  color: #d2d2d2;
  max-width: 340px;
  min-height: 50px;
  width: 330px;
  height: 60px;
  border: 0;
  margin-top: 30px;
  margin-left: 50px;
  margin-bottom: 10px;
  padding: 5px;
  font-size: 15px;
  border-radius: 10px;
}
#to-do-list textarea::placeholder {
  color: #d2d2d2;
  font-size: 15px;
  padding: 5px;
}
#to-do-list textarea:focus,
#to-do-list button:focus {
  outline: none;
}
#to-do-list button:hover {
  background-color: rgb(73, 73, 73);
}
#bMargin {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
}
#to-do-list button {
  background-color: rgb(83, 83, 83);
  height: 35px;
  width: 70px;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  font-style: italic;
}
.footer {
  position: fixed;
  bottom: 0;
  margin-top: 5%;
  width: 100%;
  background-color: rgb(41, 41, 41);
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: #d2d2d2;
}
