body {
  color: white;
}
.items {
  border-top: 1px solid #d2d2d2;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding-top: 15px;
}

li {
  background-color: rgb(204, 156, 24);
  border: none;
  border-radius: 5px;
  padding: 7px;
  width: 350px;
  margin-left: -30px;
}
.list div {
  height: 100px;
  float: right;
  cursor: pointer;
}
#pTag {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  margin-top: 0px;
}
#vertitalSlash {
  font-size: 300%;
